<template>
  <div class="wrap-nav mine-wrap">
    <div class="top">
      <div class="pic"><img src="../assets/mine/pic.png"></div>
      <p class="name">用户名</p>
    </div>
    <div class="con">
      <div class="cell"><img src="../assets/mine/comico.png">企业名单</div>
      <div class="cell"><img src="../assets/mine/userico.png">用户名单</div>
      <div class="cell" v-on:click="logoutE"><img src="../assets/mine/quitico.png">退出登录</div>
    </div>
  </div>
</template>

<script>
  import navjs from '../router/nav.js'

  export default {
    name: 'mine',
    data() {
      return {

      }
    },
    methods: {
      logoutE () {
        this.$router.push('/login')
        navjs.$emit('val', false)
      }
    }
  }
</script>

<style lang="less" scoped>
  .mine-wrap {
    .top {
      height: 4.32rem;
      width: 100%;
      background: url(../assets/mine/minebg.png) no-repeat;
      background-size: cover;
      padding-top: .8rem;
      .pic {
        width: 2rem;
        height: 2rem;
        border-radius: 200px;
        background: #fff;
        opacity: 0.8;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          width: 1.2rem;
        }
      }
      p{
        margin-top: .4rem;
        font-size: .373333rem;
        text-align: center;
      }
    }
    .con {
      .cell {
        height: .8rem;
        display: flex;
        align-items: center;
        background: #062f43;
        margin-bottom: 1px;
        padding: 0 .466667rem;
        font-size: .32rem;
        img {
          width: .4rem;
          margin-right: .4rem;
        }
      }
    }
  }
</style>



